/**
 * @description 
 * @param {string} count
 * @return {{type: int, payload: {count: int}}} action 
 */
export function addBox(count) {
    return {
        type: 'buyPetBox/boxAdded',
        payload: {
            count
        }
    }
}

/**
 * @description 
 * @param {string} count
 * @return {{type: int, payload: {price: int}}} action 
 */
export function addPrice(price) {
    return {
        type: 'buyPetBox/priceAdded',
        payload: {
            price
        }
    }
}

/**
 * @description 
 * @param {string} count
 * @return {{type: int, payload: {count: int}}} action 
 */
export function drawBox(count) {
    return {
        type: 'buyPetBox/boxDeleted',
        payload: {
            count
        }
    }
}

/**
 * @description 
 * @param {string} count
 * @return {{type: int, payload: {count: int}}} action 
 */
export function addCurrentUseBox(count) {
    return {
        type: 'currentUsePetBox/currentBoxAdded',
        payload: {
            count
        }
    }
}

