const initialState = {
    chatAwards: [],
    fashionAwards: [],
    petAwards: []
}

/**
 * @description  todos  reducer 
 * @param {object} state  initialState
 * @param {object} action 
 */
export default function awardReducer(state = initialState, action) {

    switch (action.type) {
        case 'addAward/awardAdded': {
            return {
                ...state, //  award 
                chatAwards: [
                    ...state.chatAwards, //  Redux  state  state 
                    {
                        type: action.payload.type,
                        award: action.payload.award
                    }
                ]
            }
        }
        case 'addFashionAward/fashionAwardAdded': {
            return {
                ...state,
                fashionAwards: [
                    ...state.fashionAwards,
                    action.payload.award
                ]
            }
        }
        case 'addPetAward/petAwardAdded': {
            return {
                ...state,
                petAwards: [
                    ...state.petAwards,
                    action.payload.award
                ]
            }
        }
        default: return state
    }
    
    
}